body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  cursor: url('../public/cursor.cur'), auto;
}

:root {
  --custom-cursor-hover: url('../public/hoverCursor.cur'), auto;
}

:root {
  --custom-cursor-go: url('../public/goCursor.cur'), auto;
}

:root {
  --custom-cursor-mail: url('../public/mailCursor.cur'), auto;
}

:root {
  --custom-cursor-sword: url('../public/swordCursor.cur'), auto;
}
:root {
  --custom-cursor-gear: url('../public/gearCursor.cur'), auto;
}

:root {
  --custom-cursor-bag: url('../public/bagCursor.cur'), auto;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.image-enter {
  opacity: 0;
}

.image-enter-active {
  opacity: 1;
  visibility: visible;
  transition: all 1500ms;
}

.image-enter-done {
  opacity: 1;
  visibility: visible;
}

.image-exit {
  opacity: 1;
}

.image-exit-active {
  opacity: 0;
  visibility: hidden;
  transition: all 1500ms;
}

.text-enter {
  opacity: 0;
}

.text-enter-active {
  opacity: 1;
  visibility: visible;
  transition: all 500ms;
}

.text-enter-done {
  opacity: 1;
  visibility: visible;
}

.text-exit {
  opacity: 1;
}

.text-exit-active {
  opacity: 0;
  visibility: hidden;
  transition: all 500ms;
}

@font-face {
  font-family: warcraft;
  src: url(../src/fonts/warcraft.ttf.woff);
}

@keyframes beat {
  from {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
  to {
    transform: scale(1);
  }
}

@keyframes shake {
  0% {
    transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    transform: translate(1px, -2px) rotate(-1deg);
  }
}

.horde-enter .alliance-enter {
  opacity: 0;
}
.horde-enter-active .alliance-enter-active {
  opacity: 1;
  visibility: visible;
  transition: all 900ms;
}
.horde-enter-done .alliance-enter-done {
  opacity: 1;
  visibility: visible;
}
.horde-exit .alliance-exit {
  opacity: 1;
}
.horde-exit-active .alliance-exit-active {
  opacity: 0;
  visibility: hidden;
  transform: all 900ms;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background-color: #1e2225;
}

::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-image: linear-gradient(0deg, #002958 20%, #8c0000 100%);
}
